.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

html,
body {
  height: 100%;
}

body {
  font-family: 'OpenSans-Regular', Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 1.75;
  color: #000000;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  line-height: 0;
  border: none;
  outline: none;
  background: transparent;
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'OpenSans-Bold', Arial, Helvetica, sans-serif;
  margin-top: 0;
  line-height: 1.2;
  text-transform: uppercase;
}

h1 {
  font-size: 58px;
  margin-bottom: 37px;
}

h2 {
  font-size: 42px;
  margin-bottom: 35px;
}

h3,
.h3 {
  font-size: 24px;
  margin-bottom: 24px;
}

h4,
.h4 {
  font-size: 22px;
}

a {
  text-decoration: none;
}

p {
  margin-bottom: 40px;
}

input:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
  color: transparent;
}

input:focus:-moz-placeholder,
textarea:focus:-moz-placeholder {
  color: transparent;
}

input:focus::-moz-placeholder,
textarea:focus:-moz-placeholder {
  color: transparent;
}

input:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder {
  color: transparent;
}

input {
  line-height: normal;
}

input,
textarea {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

input:focus {
  outline-offset: 0;
  outline: none;
}

ul.nav,
.filter,
.service-list,
.portfolio-list,
.team-list,
.member-contacts {
  padding-left: 0;
  -webkit-padding-start: 0;
  list-style: none;
}

.sm-debug {
  height: 100vh;
  position: fixed;
  width: 100%;
  z-index: 1000;
}

.sm-debug > div {
  height: inherit;
  max-width: 1249px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.sm-debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.sm-debug > div > div > div {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(8.333333333333334% - 30px);
  height: inherit;
  outline: 1px solid #ff0;
  background: rgba(0, 0, 0, 0.5);
}

.container {
  max-width: 1249px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

a[href^="mailto"],
a[href^="tel"] {
  color: inherit;
}

.page-header {
  padding-top: 30px;
  padding-bottom: 50px;
  margin-bottom: 100px;
  background-color: #f6f2f1;
}

.page-content {
  background-color: #ffffff;
}

.page-footer {
  background-color: #312f30;
  color: #b8b8b8;
  padding-top: 35px;
  padding-bottom: 35px;
}

.page-footer .container::after {
  display: block;
  content: "";
  clear: both;
}

.page-footer .copyright:before {
  content: "\00A9";
  margin-right: 5px;
}

.page-footer .copyright,
.page-footer .social-links {
  width: 50%;
  float: left;
}

.page-footer .social-links {
  text-align: right;
}

.page-footer .social-links a .icon-social {
  color: #312f30;
}

.page-footer .social-links a:hover .icon-social {
  color: #ff3f40;
  transition: transform 0.5s linear;
  transform: rotate(360deg);
}

.wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.video-block {
  position: relative;
  display: block;
  content: '';
  padding-top: 56.25%;
  box-shadow: -5px -3px 10px 2px #ececec;
}

.video-block iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border: none;
}

.filter {
  font-family: "OpenSans-Semibold", Arial, Helvetica, sans-serif;
  text-align: center;
  font-size: 0;
  margin-bottom: 65px;
}

.filter__item {
  display: inline-block;
  font-size: 22px;
  text-transform: uppercase;
  padding-left: 30px;
  padding-right: 30px;
}

.filter__link {
  color: #000000;
  transition: color 0.3s;
}

.filter__link:hover,
.filter__link.active {
  color: #ff3f40;
}

.btn {
  font-family: "OpenSans-Bold", Arial, Helvetica, sans-serif;
  display: inline-block;
  font-size: 18px;
  padding: 14px 64px;
  text-align: center;
  border: 2px solid #ff3f40;
  color: #ffffff;
  background-color: #ff3f40;
  border-radius: 4px;
  transition: background-color 0.3s, color 0.3s;
}

.btn:hover {
  background-color: #d61314;
}

.btn-lg {
  padding-top: 25px;
  padding-bottom: 25px;
}

.btn-service {
  background-color: #ffffff;
  color: #ff3f40;
}

.btn-service:hover {
  color: #ffffff;
  background-color: #d61314;
}

